'use strict';

(function () {
  var subLInksItems = $('.sub-links__item');

  subLInksItems.each(function (index, item) {
    var hiddenBlock = $(item).find('.sub-links__list-hidden');
    var showHiddenBlock = $(item).find('.sub-links__more a');
    var linkLabel = showHiddenBlock.text();

    showHiddenBlock.click(function (ev) {
      ev.preventDefault();
      if (!showHiddenBlock.parent().hasClass('sub-links__more_less')) {
        showHiddenBlock.parent().addClass('sub-links__more_less');
        hiddenBlock.slideDown();
        showHiddenBlock.text('Свернуть');
      } else {
        showHiddenBlock.parent().removeClass('sub-links__more_less');
        hiddenBlock.slideUp();
        showHiddenBlock.text(linkLabel);
      }
    });
  });
})();


