'use strict';

var highlightLinks = $('.highlight__anchor-link');
var highlightItem = $('.highlight__item');

highlightLinks.each(function (index, item) {
  $(item).bind('click', function (e) {
    e.preventDefault();
    var scroollToElem = $(item).attr('href');
    $('html, body').animate({
      scrollTop: $(scroollToElem).offset().top - 10
    }, 300, function () {
      if ($(scroollToElem).hasClass('highlight-animate')) {
        $(scroollToElem).removeClass('highlight-animate');
      }
      $(scroollToElem).addClass('highlight-animate');
    });
  });
});

highlightItem.each(function (index, item) {
  $(item).on('animationend webkitAnimationEnd oAnimationEnd', function () {
    $(this).removeClass('highlight-animate');
  });
});


