'use strict';

(function () {
  var tabHeads = $('.navigator__tabs-head');
  var dropdownItems = $('.navigator__tabs-dropdown');

  var innerTabHeads = $('.navigator__inner-tab-head');
  var innerDropdownItems = $('.navigator__inner-tab-dropdown');

  tabHeads.each(function (index, item) {
    $(item).click(function (ev) {
      $(this).toggleClass('active');
      dropdownItems.eq(index).slideToggle(300);
    });
  });

  innerTabHeads.each(function (index, item) {
    $(item).click(function (ev) {
      $(this).toggleClass('active');
      $(this).siblings(".navigator__inner-tab-dropdown").slideToggle(300);
    });
  });
})();


