'use strict';

(function () {
  var collapsedRow = $('#collapsed-row');
  var openCollapsed = $('.bottom-links .bottom-links__more');

  openCollapsed.click(function (ev) {
    ev.preventDefault();
    collapsedRow.toggleClass('collapsed');
    if (!collapsedRow.hasClass('collapsed')) {
      openCollapsed.text('Свернуть');
    } else {
      openCollapsed.text('Показать все');
    }
  });
})();


