'use strict';

var headerDropdownBtn = $('.header__nav-dropdown-btn');

headerDropdownBtn.each(function (index, item) {
  var closeDropdown = $(item).find('.btn-close');

  item.addEventListener('click', function () {
    item.classList.add('active');
  });
  closeDropdown.click(function (e) {
    e.stopPropagation();
    item.classList.remove('active');
  });
});


